import NewsChat from "./newsChat-model.js";
const newsChat = new NewsChat();
export default {
	data() {
		return {
			options: {},
			updataTimes: null,
			userInfo: {},
			message: '',
			newsNode: {},
			newList: []
		};
	},
	onLoad(options) {
		// console.log(options)
		const that = this
		that.options = options
		that._onLoad()
	},
	onShow() {
		
	},
	onUnload(){  
		this.runClearInterval()
	},
	onHide() {
		this.runClearInterval()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.getNewsList(() => {
				callBack && callBack();
			})
			that.updataTimes = setInterval(() => {
				that.updataNewsList()
			}, 3000);
		},
		// 刷新消息
		updataNewsList() {
			const that = this
			uni.request({
				url: `${newsChat.base_qequest_url}chat/getNewMessage`,
				data: {
					openid: that.userInfo.openid,
					id: that.options.id
				},
				method: 'POST',
				success: (res) => {
					if (res.data.code == 4000) {
						let newsNode = res.data.data
						let newMessageList = []
						for (let i = 0; i < newsNode.length; i++) {
							newMessageList.push({
								content: newsNode[i].content,
								direction: 2,
								portrait: newsNode[i].portrait,
								nickname: newsNode[i].nickname
							})
						}
						that.newList = that.newList.concat(newMessageList)
					}
				}
			});
		},
		// 初始化消息
		getNewsList(callBack) {
			const that = this
			newsChat.getNewsList({
				openid: that.userInfo.openid,
				id: that.options.id
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					let newNode = res
					that.newList = newNode.data.reverse()
					that.newsNode = res
				}
				callBack && callBack();
			})
		},
		
		// 发送消息
		sendMessage(callBack) {
			const that = this
			newsChat.sendMessage({
				openid: that.userInfo.openid,
				id: that.options.id,
				content: this.message
			}, (res) => {
				if (res.code == 4000) {
					// 给消息列表中加入自己发送的消息
					let myNewMessage = {
						content: that.message,
						direction: 1,
						portrait: that.userInfo.portrait
					}
					that.newsNode.data.push(myNewMessage)
					// 清空输入的消息
					that.message = ''
				} else {
					newsChat.show_tips(res.explain)
				}
				// callBack && callBack();
			})
		},
		runClearInterval() {
			if(this.updataTimes) {
			    clearInterval(this.updataTimes);  
			    this.updataTimes = null;  
			}
		},
		getMessage(e) {
			const that = this
			this.message = newsChat.get_input_val(e)
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return newsChat.onShareAppMessage({});
	}
}
